.slider {
    position: relative;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    height: 600px;
}
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: #ededed;
    color: var(--clr-sub);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: #ededed;
    color: var(--clr-sub);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }


